import React, { useState } from 'react';
import {
  Box,
  Typography,
  Grid,
  Paper,
  Button,
  Container,
  useTheme,
  Stepper,
  Step,
  StepLabel,
  StepContent,
} from '@mui/material';
import { motion } from 'framer-motion';
import DataObjectIcon from '@mui/icons-material/DataObject';
import PsychologyIcon from '@mui/icons-material/Psychology';
import TimelineIcon from '@mui/icons-material/Timeline';
import CodeIcon from '@mui/icons-material/Code';

const steps = [
  {
    label: 'What is Machine Learning?',
    description: 'Machine learning is a branch of artificial intelligence that focuses on building applications that learn from data and improve their accuracy over time without being explicitly programmed to do so.',
    icon: <PsychologyIcon sx={{ fontSize: 40 }} />,
  },
  {
    label: 'Key Components',
    description: 'The main components of machine learning include data, features, algorithms, and models. Data is the foundation, features are the input variables, algorithms are the methods used to learn patterns, and models are the learned representations.',
    icon: <DataObjectIcon sx={{ fontSize: 40 }} />,
  },
  {
    label: 'How it Works',
    description: 'Machine learning works by training models on historical data to identify patterns and make predictions. The process involves data collection, preprocessing, model training, evaluation, and deployment.',
    icon: <TimelineIcon sx={{ fontSize: 40 }} />,
  },
  {
    label: 'Key Concepts',
    description: 'Important concepts include supervised learning, unsupervised learning, reinforcement learning, training data, test data, features, labels, and model evaluation metrics.',
    icon: <CodeIcon sx={{ fontSize: 40 }} />,
  },
];

const Introduction: React.FC = () => {
  const [activeStep, setActiveStep] = useState(0);
  const theme = useTheme();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ py: 8 }}>
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <Typography
            variant="h2"
            align="center"
            gutterBottom
            sx={{
              fontWeight: 700,
              background: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.secondary.main})`,
              backgroundClip: 'text',
              textFillColor: 'transparent',
              mb: 2,
            }}
          >
            Introduction to Machine Learning
          </Typography>
          <Typography
            variant="h5"
            align="center"
            color="text.secondary"
            paragraph
            sx={{ mb: 6 }}
          >
            A step-by-step guide to understanding the fundamentals
          </Typography>
        </motion.div>

        <Grid container spacing={4}>
          <Grid item xs={12} md={8}>
            <motion.div
              initial={{ opacity: 0, x: -20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
            >
              <Paper
                elevation={0}
                sx={{
                  p: 4,
                  borderRadius: 4,
                  background: 'background.paper',
                  boxShadow: '0 4px 20px rgba(0, 0, 0, 0.05)',
                }}
              >
                <Stepper activeStep={activeStep} orientation="vertical">
                  {steps.map((step, index) => (
                    <Step key={step.label}>
                      <StepLabel
                        StepIconProps={{
                          sx: {
                            color: activeStep >= index ? 'primary.main' : 'text.disabled',
                          },
                        }}
                      >
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          {step.icon}
                          <Typography variant="h6" sx={{ fontWeight: 600 }}>
                            {step.label}
                          </Typography>
                        </Box>
                      </StepLabel>
                      <StepContent>
                        <Typography
                          sx={{
                            mt: 2,
                            mb: 2,
                            color: 'text.secondary',
                            lineHeight: 1.8,
                          }}
                        >
                          {step.description}
                        </Typography>
                        <Box sx={{ mb: 2 }}>
                          <Button
                            variant="contained"
                            onClick={handleNext}
                            sx={{ mr: 1 }}
                          >
                            {index === steps.length - 1 ? 'Finish' : 'Continue'}
                          </Button>
                          <Button
                            disabled={index === 0}
                            onClick={handleBack}
                            sx={{ mr: 1 }}
                          >
                            Back
                          </Button>
                        </Box>
                      </StepContent>
                    </Step>
                  ))}
                </Stepper>
              </Paper>
            </motion.div>
          </Grid>
          <Grid item xs={12} md={4}>
            <motion.div
              initial={{ opacity: 0, x: 20 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.5 }}
            >
              <Paper
                elevation={0}
                sx={{
                  p: 4,
                  height: '100%',
                  borderRadius: 4,
                  background: `linear-gradient(45deg, ${theme.palette.primary.main}, ${theme.palette.secondary.main})`,
                  color: 'white',
                }}
              >
                <Typography variant="h5" gutterBottom sx={{ fontWeight: 700 }}>
                  Key Takeaways
                </Typography>
                <Box component="ul" sx={{ mt: 2, pl: 2 }}>
                  <Typography component="li" sx={{ mb: 2 }}>
                    Machine learning enables computers to learn from data without explicit programming
                  </Typography>
                  <Typography component="li" sx={{ mb: 2 }}>
                    The process involves data collection, preprocessing, model training, and evaluation
                  </Typography>
                  <Typography component="li" sx={{ mb: 2 }}>
                    Different types of learning include supervised, unsupervised, and reinforcement learning
                  </Typography>
                  <Typography component="li" sx={{ mb: 2 }}>
                    Model performance is evaluated using various metrics and validation techniques
                  </Typography>
                </Box>
              </Paper>
            </motion.div>
          </Grid>
        </Grid>
      </Box>
    </Container>
  );
};

export default Introduction;
